.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
ul{
    list-style: none;
    padding: 0;
    margin: 0;
}
.controls{
    height: 45px;
    position: relative;
}
.createNewTag{
}
.leftControl{
    position:relative;
    left:2px;
    float: left;
}
.centerControl{
}
.rightControl{
    position: relative;
    right:2px;
    float: right;
}
.main{
    position: absolute;
    width: calc(100% - 15px);
    height: 100%;
    padding-right: 10px;
    padding-left: 10px;
}
.top{
    top: 0px;
    height: 45px;
}
.addAndStart{
    float: left;
}
.add{
    position: relative;
    float: right;
    right: 2px;
}
.body{
    position: relative;
    width: 100%;
    height: -webkit-calc(100% - 100px);
    height:    -moz-calc(100% - 100px);
    height:         calc(100% - 100px);
}
.inputTask{
    height: 30px;
    top: 2px;
    vertical-align: middle;
    float: left;
    width: calc(100% - 90px);
}
.leftButton{
    height: 30px;
    float: left;
}
.rightButton{
    height: 30px;
    float: right;
}
.oneTask{
    width: 100%;
    height: 45px;
}
.addFriend{
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
